
//     DOM
document.addEventListener('DOMContentLoaded', function() {
  // Clients slider -    
  const clientsSlider = $('.clients__slider').owlCarousel({
    loop: true,
    margin: 30,
    nav: false,
    dots: false,
    autoplay: false,
    autoplaySpeed: 800,
    autoplayHoverPause: true,
    smartSpeed: 1000,
    items: 4,
    center: false,
    startPosition: 0,
    responsive: {
      0: {
        items: 1,
        center: true,
        margin: 20,
      },
      601: {
        items: 4,
        center: false,
        margin: 30,
      }
    }
  });

  // Intersection Observer   clients 
  const clientsObserver = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        //     -  
        clientsSlider.trigger('play.owl.autoplay');
      } else {
        //     -  
        clientsSlider.trigger('stop.owl.autoplay');
      }
    });
  }, {
    threshold: 0.1
  });

  //   clients 
  const clientsElement = document.querySelector('.clients__slider');
  if (clientsElement) {
    clientsObserver.observe(clientsElement);
  }

  // Team slider -    
  $('.team__slider').owlCarousel({

    margin: 30,


    items: 4,

    responsive: {
      0: {
        items: 1,
        center: true,
        margin: 20,
      },
      601: {
        items: 4,
      }
    }
  });
});
